.card:hover {
    box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.4);
    transform: scale(1.1);
    transition: 0.5s ease-in-out;
    cursor: pointer;
}

.back:hover {
    box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.4);
    transform: scale(0.95);
    transition: 0.5s ease-in-out;
    cursor: pointer;
}

.home {
    background-image: url(C:\Users\Lenovo\Desktop\my-port\src\components\pngwing.com.png);
    background-repeat: repeat;
}


.second:hover {
    transition: .5s ease-in-out; 
    transform: skew(-180deg, 180deg);
    cursor: pointer;
    transition-duration: 4s;
}

.Email {
    background-image: url(C:\Users\Lenovo\Desktop\my-port\src\components\background-home.webp);
    background-repeat: no-repeat;
    width: 92%;
    height: 40vh;
    display: grid;
    align-content: center;
}

@media screen and (max-width:1420px) {
    .slide {
        height: 100vh !important;
    }

    .card {
        font-size: 12px;
        margin: 10px;
    }

    .home .second img{
        right: 20px !important;
    }

}

